.cta__form {
  max-width: 1190px;
  margin-bottom: 90px;
  padding: 50px 80px;
  border-radius: 30px;
  background: 
    url('/img/cta__form-bg.svg') right -80px / auto no-repeat,
    linear-gradient(180deg, white 0%, #FFF4D7 100%) center bottom no-repeat;
  box-shadow: 0 4px 64px 0 rgba(black, .05);

  form {
    padding-top: 30px;
  }

  input {
    display: inline-block;
    height: 56px;
    margin-right: 15px;
    padding: 0 30px;
    border-radius: 8px;
    border: 1px solid #E7E7E7;
    line-height: 1;
    font-size: 20px;
    font-weight: 600;
    
    &::placeholder {
      color: #AEAEAE
    }
  }
}