.header__main {
  // Home
  &.hero--home {
    background: 
      url('/img/hero__laptop.png') right 45% / 56vw no-repeat,
      url('/img/hero__home-bg.svg') right top no-repeat;

    .header__hero {
      justify-content: flex-start;
      align-items: flex-start;

      > div {
        width: 50%;
        margin: initial;
        text-align: left;
      }

      h1 {
        + p {
          margin: 30px 0 60px;
          line-height: 1.4;
          font-size: toem(22px);
        }
      }
    }
  }
}