.header__main {
  // Home
  &.hero--platform {
    position: relative;
    background: 
      url('./img/hero--bg.svg') bottom center / auto no-repeat,
      linear-gradient(180deg, white 0%, #EAFDC1 50%, white 80%) center 180px no-repeat;
    
    &::after {
      @include c-separator(bottom, 4px, green);

      content: "";
      position: absolute;
      bottom: -4px;
      width: 100%;
      height: 4px;

    }
  }
}