.post {
  margin-bottom: 200px;

  .floating-tag {
    position: relative;
    width: max-content;
    opacity: 1;
  }

  > header {
    position: relative;
    max-width: 930px;
  }

  h1 {
    @extend h2;

    margin: 45px 0;
  }

  .post__thumb {
    margin-bottom: 70px;

    img {
      display: block;
      width: 100%;
      border-radius: 40px;
      box-shadow: 0 5px 75px 0 rgba(black, .05);
    }
  }

  .post__content {
    display: grid;
    grid-template-columns: 1.8fr minmax(50px, .2fr);
    gap: 30px;
  }
}