.pagination {
  padding-top: 40px;
  text-align: center;

  .page-numbers {
    display: inline-block;
    width: 38px;
    padding: 10px;
    border-radius: 9999px;
    font-size: 80%;
    color: $suit-color--dark;
    background-color: $suit-color;

    &.prev,
    &.next {
      width: auto;
    }

    &:hover,
    &:focus,
    &.current {
      color: $suit-color;
      background-color: $suit-color;
    }
  }
}