////
/// Buttons
/// @group components
/// @author Dami√°n Muti
////


///
$c-button-text-color: $suit-color !default;

:root {
  ///
  --c-button-padding: #{toem(16px)} #{toem(25px)};
  ///
  --c-button--bg-border: #B2B2B2;
  ///
  --c-button--bg-top: white;
  ///
  --c-button--bg-bottom: white;
  ///
  --c-button--bg-hover: white;
}

/// General styles for buttons. There are also a couple of modifier classes to handle smaller and bigger styles for the buttons.
/// @example markup - Regular size buttons
///   <button class="button"></button>
/// @example markup - Small size buttons
///   <button class="button button--small"></button>
/// @example markup - Large size buttons
///   <button class="button button--large"></button>

@mixin c-button() {
  .button {
    appearance: none;
    display: inline-block;
    padding: var(--c-button-padding);
    border: 1px solid;
    border-radius: torem(8px);
    border-color: var(--c-button--bg-border);
    line-height: 1; // Normalize height
    font-family: $suit-font-family--primary;
    font-size: toem(20px);
    font-weight: 600;
    color: $c-button-text-color;
    text-align: center;
    text-decoration: none;
    background: linear-gradient(180deg, var(--c-button--bg-top) 0%, var(--c-button--bg-bottom) 50%, var(--c-button--bg-hover) 100%);
    background-size: 100% 200%;
    transition: all $suit-transition-duration $suit-transition-timing;

    &:hover {
      background-position: 0 100%;
    }

    &.button--orange {
      --c-button--bg-border: #{$suit-color--orange};
      --c-button--bg-top: #{tint($suit-color--orange, 80%)};
      --c-button--bg-bottom: #{tint($suit-color--orange, 60%)};
      --c-button--bg-hover: #{tint($suit-color--orange, 40%)};
    }

    &.button--lightblue {
      --c-button--bg-border: #{$suit-color--lightblue};
      --c-button--bg-top: #{tint($suit-color--lightblue, 75%)};
      --c-button--bg-bottom: #{tint($suit-color--lightblue, 50%)};
      --c-button--bg-hover: #{tint($suit-color--lightblue, 20%)};
    }

    &.button--outline {
      padding-left: 30px;
      padding-right: 30px;
      border-width: 1px !important;
    }

    &.button--fw {
      --c-button-padding: #{toem(14px)} #{toem(20px)} !important;

      width: 100%;
    }

    &.button--medium {
      --c-button-padding: #{toem(18px)} #{toem(30px)};

      border-width: 2px;
      font-size: 20px;
    }

    &.button--small {
      --c-button-padding: #{toem(9px)} #{toem(12px)};

      border-width: 2px
    }
  }
}

@include c-button();

///
$mobile-menu-button-transition: $suit-transition-duration + ' ' + $suit-transition-timing !default;

/// Mixin for generating the burguer-ish button used in mobile menus. This button is also transformed into an X when adding the <code>active</code> class to the element (as an indicator for closing the mobile menu).
/// @param {size} $width - Total width of the button.
/// @param {size} $height - Total height of the button.
/// @param {size} $stroke - Line width.
/// @param {color} $color - Main button color.
/// @param {string} $transition [SUIT default global setting] - Transition timing used for animation.
/// @example scss - Using the mixin
///   @include c-mobile-menu-button(23px, 14px, 2px, #424242);
/// @example markup - For the button to work as intended, the element must have three spans within.
/// <div class="mobile-menu-button">
///   <span></span>
///   <span></span>
///   <span></span>
/// </div>
/// @todo Maybe adding different animations?

@mixin c-mobile-menu-button($width, $height, $stroke, $color) {
  $sin45: 0.70710678118; //sqrt(2)/2 = sin(45 deg)
  $cos45: $sin45;
  $offset-y: ($width * $sin45 - $height + $stroke) / 2;
  $offset-x: ($width * $cos45 - $width) / 2;

  .mobile-menu-button {
    position: relative;
    width: $width;
    height: $height;
    transform: rotate(0deg);
    cursor: pointer;

    span {
      position: absolute;
      left: 0;
      display: block;
      opacity: 1;
      width: 100%;
      height: $stroke;
      border-radius: calc(#{$stroke} / 2);
      background: $color;
      transform: rotate(0deg);
      transition: #{$mobile-menu-button-transition};
      transform-origin: left center;

      &:nth-child(1) {
        top: 0;
      }

      &:nth-child(2) {
        top: calc(50% - #{$stroke} / 2);
      }

      &:nth-child(3) {
        bottom: 0;
      }
    }
    &.active {
      span {
        &:nth-child(1) {
          transform: rotate(45deg);
          top: -($offset-y);
          left: -($offset-x);
        }

        &:nth-child(2) {
          width: 0%;
          opacity: 0;
        }

        &:nth-child(3) {
          transform: rotate(-45deg);
          bottom: -($offset-y);
          left: -($offset-x);
        }
      }
    }
  }
}
