.header__main {
  // Home
  &.hero--our-mission {
    position: relative;
    background: 
      url('/img/hero--bg.svg') bottom center / auto no-repeat,
      linear-gradient(180deg, white 0%, #E5F9FF 100%) center 180px no-repeat;
    
    &::after {
      @include c-separator(bottom, 4px, lightblue);

      content: "";
      position: absolute;
      bottom: -4px;
      width: 100%;
      height: 4px;

    }

    .header__hero {
      > div {
        max-width: 55vw;
      }
    }
  }
}