.badge {
  position: relative;
  width: 75px;
  height: 75px;
  max-width: 75px;
  max-height: 75px;
  min-width: 75px;
  min-height: 75px;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  border-radius: 9999px;
  background-color: var(--bg);

  &--small {
    width: 42px;
    height: 42px;
    max-width: 42px;
    max-height: 42px;
    min-width: 42px;
    min-height: 42px;
    font-size: 57%;
  }

  &--big {
    width: 100px;
    height: 100px;
    max-width: 100px;
    max-height: 100px;
    min-width: 100px;
    min-height: 100px;
    font-size: 135%;
  }

  &--image {
    @include respond-to('tablet-v') {
      width: 145px;
      height: 145px;
      max-width: 145px;
      max-height: 145px;
      min-width: 145px;
      min-height: 145px;
      
      img {
        max-height: 95px;
      }
    }

    @include respond-to('mobile-v') {
      width: 100px;
      height: 100px;
      max-width: 100px;
      max-height: 100px;
      min-width: 100px;
      min-height: 100px;
      
      img {
        max-height: 75px;
      }
    }

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $suit-color;
    width: 175px;
    height: 175px;
    max-width: 175px;
    max-height: 175px;
    min-width: 175px;
    min-height: 175px;
    padding: 30px;
    text-align: center;
    
    img {
      vertical-align: middle;
    }

    &::before {
      content: '';
    }
  }

  &::before {
    @extend .suit-icon;

    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 200%;
    color: white;
    transform: translate(-50%, -50%);
    transition: all $suit-transition-duration $suit-transition-timing;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background-color: rgba(white, 0.2);
  }

  + p {
    @include respond-to('ismobile') {
      margin-top: 15px;
    }

    margin: 30px 0 0;
  }
}