.header__main {
  // Home
  &.hero--request-a-demo {
    background: 
      url('/img/hero__home-bg.svg') right top no-repeat;

    .header__hero {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 5vw;
      padding-top: 170px;
      
      > div {
        text-align: left;
      }
    }

    menu {
      display: none;
    }

    h1 {
      + p {
        margin: 30px 0 60px;
        line-height: 1.4;
        font-size: toem(18px);
      }
    }

    ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
      list-style-position: inside;
      font-size: 20px;

      .suit-icon {
        margin: -4px 10px 0 0;
        font-size: 22px;
      }

      li {
        padding-bottom: 15px;
      }
    }

    figure {
      position: relative;
      margin-top: 50px;

      &::after {
        @include c-separator(bottom, 4px, multicolor);
  
        content: "";
        position: absolute;
        left: -30px;
        bottom: 0;
        width: calc(100% + 60px);
        height: 4px;
  
      }
    }

    img {
      display: block;
      width: 100%;
    }
  }
}

.do--request-a-demo {
  padding: 50px 50px 40px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 5px 100px 0 rgba(black, .05);
  transition: box-shadow .25s ease;

  &:focus-within {
    box-shadow: 0 5px 100px 0 rgba(black, .15);
  }

  fieldset {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px 40px;
  }

  legend {
    grid-column: span 2;
    font-size: 23px;
    margin-bottom: 40px;
  }

  p {
    margin: 0;

    &:nth-last-of-type(1),
    &:nth-last-of-type(2) {
      grid-column: span 2;
    }
  }

  label {
    font-size: 16px;
    font-weight: 500;
  }

  input {
    appearance: none;
    outline: none;
    width: 100%;
    display: block;
    height: 53px;
    margin-top: 5px;
    padding: 0 20px;
    border: 0;
    border-radius: 8px;
    line-height: 1;
    font-family: $suit-font-family--primary;
    font-weight: 500;
    font-size: 18px;
    background-color: rgba(#585858, .04);
    transition: background-color .25s ease;

    &:focus {
      background-color: rgba(#585858, .09);;
    }
  }

  footer {
    grid-column: span 2;
    
    p {
      margin: 5px 0 30px;
      font-size: 18px;
    }

    a {
      font-weight: 400;
      color: $suit-color;
      text-decoration: underline;
    }
  }
}