@keyframes do--boxshadow {
  to {
    box-shadow: 20px 25px 0 0 rgba(#D4FB84, .6);
  }
}

.section--specialized {
  background: url('/img/lines--lightblue.svg') left 50px / auto no-repeat;

  .wrapper {
    padding-top: 9vw;
    padding-bottom: 26vw;
  }

  figure {
    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }

  img {
    display: block;
    width: 100%;
    max-width: 615px;
    animation: do--boxshadow 1s 1s ease forwards;
  }

  .floating-tag {
    @for $i from 1 to 5 {
      &:nth-of-type(#{$i}) {
        animation: fade-in .75s #{($i * 0.5)}s ease forwards;
      }
    }

    &--green {
      top: 45%;
      left: -50px;
    }

    &:nth-child(2) {
      top: 15%;
      right: -40px;
    }

    &:last-of-type {
      bottom: 15%;
      right: -10px;
    }
  }
}

@keyframes isologo {
  from {
    opacity: 0;
    transform: scale(.75) rotate(0);
  }

  to {
    opacity: 1;
    transform: scale(1) rotate(1turn);
  }
}

@keyframes do--text {
  from {
    transform: translateY(-10px);
    opacity: 0;
  }
  to {
    transform: translateY(-0);
    opacity: 1;
  }
}

.section--unify {
  position: relative;
  z-index: 10;
  margin-top: -16vw;
  background: linear-gradient(180deg, rgba(250,248,235,0) 0%, rgba(255,253,243,1) 17%, rgba(255,253,237,1) 100%);

  .wrapper {
    padding-top: 12vw;
  }

  svg {
    direction: ltr;
    
    text {
      font-family: $suit-font-family--primary;
      cursor: default;
    }
  }

  .isologo {
    opacity: 0;
    transform-origin: center;
    animation: isologo 1.5s 0s ease forwards; 
  }

  .circle {
    opacity: 0;
    
    @for $i from 1 to 10 {
      &:nth-child(#{$i}) {
        animation: fade-in .3s #{$i * .075}s ease forwards;

        text {
          animation: do--text .3s #{$i * .12}s ease forwards;
        }
      }
    }

    text {
      transform: translateY(-10px);
      opacity: 0;
    }
  }
}


@keyframes do--fade-down {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@mixin do--slider($position, $element, $delay) {
  @keyframes do--slider-#{$element} {
    to {
      transform: translateX(0);
    }
  }

  transform: translateX($position);
  animation: do--slider-#{$element} .75s $delay ease forwards;
}

.section--evaluate {
  background: url('/img/lines--orange.svg') top left / auto 100% no-repeat;

  .wrapper {
    padding-top: 11vw;
    padding-bottom: 24vw;
  }

  figure {
    text-align: center;
    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }

  svg {
    max-width: 400px
  }

  .stroke-1 {
    @include do--stroke(170px, top, .65s);
  }

  .stroke-2 {
    @include do--stroke(90px, top, .65s);
  }

  .slider-1 {
    @include do--slider(-130px, 'slider-1', .65s);
  }

  .slider-2 {
    @include do--slider(-89px, 'slider-2', .65s);
  }

  .slider-3 {
    @include do--slider(-33px, 'slider-3', .65s);
  }

  .slider-4 {
    @include do--slider(-158px, 'slider-4', .65s);
  }
}

@mixin do--dots($delay) {
  opacity: 0;
  animation: fade-in .75s $delay ease forwards;
}

@mixin do--data($delay) {
  opacity: 0;
  transform: translateY(-10px);
  animation: do--text 1s $delay ease forwards;
}

.section--analyze {
  position: relative;
  z-index: 10;
  margin-top: -16vw;
  background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(229,249,255,1) 65%, rgba(243,252,255,1) 100%);

  .wrapper {
    padding-top: 20vh;
  }

  .dot-1 {
    @include do--dots(0s)
  }

  .dot-2 {
    @include do--dots(.2s)
  }

  .dot-3 {
    @include do--dots(.4s)
  }

  .dot-4 {
    @include do--dots(.6s)
  }

  .dot-5 {
    @include do--dots(.8s)
  }

  .data-1 {
    @include do--data(.9s)
  }

  .data-2 {
    @include do--data(1.1s)
  }

  .data-3 {
    @include do--data(1.3s)
  }

  .data-4 {
    @include do--data(1.5s)
  }

  .data-5 {
    @include do--data(1.7s)
  }
}

.section--engage {
  position: relative;
  z-index: 10;
  background: url('/img/lines--green.svg') left 30px / auto no-repeat;

  .wrapper {
    padding-top: 20vw;
    padding-bottom: 35vw;
  }

  img {
    display: block;
    border-radius: 10px;
    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }

  + .main__cta {
    margin-top: -28vw;
  }
}