.page--platform {
  .header__hero {
    //@include c-separator(bottom, 4px, green);
  }
}

@keyframes do--arrow {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.section--actionableinsights {
  .wrapper {
    @include respond-to('tablet-v'){
      background-size: 90%;
    }
    @include respond-to('mobile-v'){
      padding-top: 20vw;
      padding-bottom: 20vw;
      gap: 50px;
    }

    padding-top: 11vw;
    padding-bottom: 11vw;
    background: url('./img/line--green-left.svg') left 5vw / auto no-repeat;
  }

  figure {
    @include respond-to('tablet-v'){
      max-width: 45% !important;
      min-width: 220px !important;
    }

    text-align: center;
    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }

  svg {
    max-width: 320px
  }

  .stroke-1 {
    @include do--stroke(140px, bottom, .65s);
    
    transform-box: fill-box;
  }

  .arrow {
    opacity: 0;
    transform: translateX(-15px);
    animation: do--arrow .5s 2s ease forwards;
  }

  .floating-tag {
    @for $i from 1 to 5 {
      &:nth-of-type(#{$i}) {
        animation: fade-in .75s #{.85 + ($i * 0.5)}s ease forwards;
      }
    }

    &:nth-child(1) {
      @include respond-to('mobile-v'){
        top: 40%;
        left: -10%;
      }

      top: 80px;
      left: 30px;
    }

    &:nth-child(2) {
      @include respond-to('mobile-v'){
        bottom: 20%;
        right: -10%;
      }

      bottom: 165px;
      right: 85px;
    }
  }
}

.section--employeewellness {
  margin-top: -9vw;

  .wrapper {
    @include respond-to('tablet-v'){
      padding-top: 35vw;
      padding-bottom: 25vw;
      background-size: 65%;
    }

    grid-template-columns: 1.1fr .9fr;
    padding-top: 18vw;
    padding-bottom: 11vw;
    background: url('./img/line--orange-right.svg') right 0 / auto no-repeat;

    > div {
      padding-right: 3vw;
    }
  }

  figure {
    @include respond-to('tablet-v'){
      min-width: 80%;
    }
    @include respond-to('mobile-v'){
      max-width: 90%;
    }

    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }

  img {
    border-radius: 6px;
    box-shadow: 0 4px 60px 0 rgba(black, .1);
  }

  .floating-tag {
    @for $i from 1 to 5 {
      &:nth-of-type(#{$i}) {
        animation: fade-in .75s #{.35 + ($i * 0.5)}s ease forwards;
      }
    }

    &:nth-child(1) {
      @include respond-to('mobile-v'){
        top: 10%;
        left: 0;
      }

      top: -60px;
      left: 50px;
    }

    &:nth-child(2) {
      @include respond-to('mobile-v'){
        right: 0;
      }

      bottom: 40px;
      right: -40px;
    }
  }
}

