.main__cta {
  display: flex;
  justify-content: center;
  align-items: center;
  background: 
    url('/img/cta__bg.svg') bottom center / cover no-repeat,
    linear-gradient(180deg, rgba(white, 0) 0%, rgba(243,254,235,1) 100%);
  
  .wrapper {
    position: relative;
    z-index: 20;
    padding-top: 13vw;
    padding-bottom: 100px;
    text-align: center;
  }

  p {
    margin: 30px 0;
    font-size: 18px;
  }
}