.section--featured-resource {
  max-width: 1190px;
  min-height: 320px;
  margin: -150px auto 0;

  article {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    min-height: 320px;
  }

  figure {
    width: 100%;
    margin: 0;
    padding-right: 61%;
  }

  img {
    position: relative;
    z-index: 20;
    display: block;
    border-radius: 30px;
    box-shadow: 0 12px 22px 8px rgba(black, .1);
  }

  div {
    position: absolute;
    top: 50%;
    max-width: 778px;
    margin-left: 32%;
    padding: 60px 40px 60px 10%;
    border-radius: 30px;
    background: linear-gradient(180deg, rgba(255,199,54,1) 0%, rgba(255,199,54,.4) 100%);
    transform: translateY(-50%);

    &::after {
      content: "";
      position: absolute;
      z-index: 0;
      top: 2px;
      left: 2px;
      width: calc(100% - 4px); // 2px border each side
      height: calc(100% - 4px); // 2px border each side
      border-radius: 28px;
      background-color: white;
    }

    > * {
      position: relative;
      z-index: 20;
    }
  }

  h2 {
    line-height: 1.1;
    margin-bottom: 30px;
  }
  
  a {
    font-weight: 600;
    color: shade($suit-color--lightblue, 11%);

    &:hover {
      .suit-icon {
        transform: translateX(5px);
      }
    }
  }

  .suit-icon {
    padding-left: 5px;
    transition: transform .25s ease;
  }
}

.insights__filters {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3.5%;
  max-width: 740px;
  margin: 60px auto 130px;
  padding-left: 4.5%;
  border-left: 1px solid #E7E7E7;

  a {
    border-radius: 9999px;
    padding: 16px 30px;
    font-weight: 600;
    color: #AEAEAE;

    &:hover {
      color: $suit-color;
      background-color: white;
      box-shadow: 
        0 0 0 1px #E7E7E7,
        0 4px 60px 0 rgba(black, .1);
    }

    &:active,
    &.is--active {
      color: $suit-color;
      background: linear-gradient(180deg, rgba(234,253,193,1) 0%, rgba(194,252,150,1) 100%);
      box-shadow: 0 0 0 2px #90F56C;
    }
  }
}

.insights__list {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 5vw 2.5%;
  max-width: 1190px;
  min-height: 40vh;

  article  {
    border-radius: 30px;
    background-color: white;
    box-shadow: 0 4px 64px 0 rgba(black, .05);
    transition: box-shadow .25s ease;

    &:hover {
      box-shadow: 0 4px 64px 0 rgba(black, .13);

      figure {
        transform: translateY(calc(-30% - 2px));
      }

      button {
        .suit-icon {
          transform: translateX(5px);
        }
      }
    }
  }

  a {
    position: relative;
    display: block;
    padding: 20px;
    padding-top: 39%;
    color: $suit-color;
  }

  figure {
    position: absolute;
    top: 0;
    width: calc(100% - 40px);
    margin: 0;
    transform: translateY(-30%);
    transition: transform .25s ease;
  }

  img {
    display: block;
    width: 100%;
    border-radius: 22px;
  }

  .insights__tag {
    margin-bottom: 20px;
    font-size: 15px;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 3px;
  }

  p {
    margin: 10px 0 20px;
    font-size: 12px;
  }

  button {
    appearance: none;
    padding: 0;
    border: none;
    font-size: 20px;
    font-weight: 600;
    color: #23B9E7;
    background: transparent;

    .suit-icon {
      padding-left: 5px;
      font-size: 16px;
      transition: all .25s ease;
    }
  }
}

.insights__actions {
  display: flex;
  align-items: center;
  justify-content: space-between;

  div.suit-icon {
    font-size: 24px;
  }
}

@keyframes do--preloader {
  to {
    transform: rotate(359deg);
  }
}

.insights__preloader {
  position: absolute;
  top: 45%;
  left: 50%;
  width: 48px;
  height: 48px;
  transform: translate(-50%, -45%);

  svg {
    opacity: 0;
    transform-origin: center;
  }

  &.is--visible {
    svg {
      animation: 
        fade-in 1s 0s ease forwards,
        do--preloader 1.3s 0s linear infinite;
    }
  }
}

.insights__pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin: 0;
  padding: 80px 0 130px;

  ol {
    display: flex;
    margin: 0;
    padding: 0 30px;
    list-style: none;
  }

  a {
    padding: 15px;
    color: rgba($suit-color, .3);
    text-decoration: none !important;

    &:hover,
    &.is--active {
      color: rgba($suit-color, 1);
    }

    &.suit-icon {
      &:first-of-type {
        transform: translateX(0) rotate(-.5turn);

        &:hover {
          transform: translateX(-5px) rotate(-.5turn);
        }
      }
      
      &:hover {
        transform: translateX(5px);
      }

      &.is--disabled {
        pointer-events: none;
        color: rgba($suit-color, .15);
      }
    }
  }
}