.header__main {
  min-height: 200px;
}

.header__logo {
  width: 258px;
}

.nav__main {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 140px;

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  menu {
    display: grid;
    grid-auto-flow:column;
    align-items: center;
    gap: 30px;

    a {
      @include do--menu-links;
    }
  }
}

@keyframes paint-text {
  to {
    width: 100%;
  }
}

.header__hero {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 240px;
  padding-bottom: 150px;
  
  > div {
    text-align: center;
    max-width: 50vw;
  }

  h1 {
    em { 
      &::before {
        width: 0;
        animation: paint-text .65s 0s ease forwards;
      }

      &:nth-of-type(2) {
        &::before {
          animation-delay: .15s
        }
      }
    }

    + p {
      margin: 40px 0 60px;
      line-height: 1.4;
      font-size: 32px;
    }
  }
}

.header__logos {
  @include c-separator(bottom);

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10%;
    height: 130px;
  }

  img {
    max-width: 138px;
  }
}