.page--solutions {
  .header__hero {
    //@include c-separator(bottom, 4px, green);
  }
}

@keyframes do--arrow {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.section--actionableinsights {
  .wrapper {
    padding-top: 11vw;
    padding-bottom: 11vw;
    background: url('/img/line--green-left.svg') left 5vw / auto no-repeat;
  }

  figure {
    text-align: center;
    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }

  svg {
    max-width: 400px
  }

  .stroke-1 {
    @include do--stroke(140px, bottom, .65s);
  }

  .arrow {
    opacity: 0;
    transform: translateX(-15px);
    animation: do--arrow .5s 2s ease forwards;
  }

  .floating-tag {
    @for $i from 1 to 5 {
      &:nth-of-type(#{$i}) {
        animation: fade-in .75s #{.85 + ($i * 0.5)}s ease forwards;
      }
    }

    &:nth-child(1) {
      top: 80px;
      left: 30px;
    }

    &:nth-child(2) {
      bottom: 165px;
      right: 85px;
    }
  }
}

