.share {
  position: sticky;
  top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 30px;
  
  header {
    font-size: 16px;
    font-weight: 700;
  }

  .icon {
    rect {
      fill: #424242;
      transition: fill .25s ease;
    }

    &--facebook {
      &:hover {
        rect {
          fill: #0866FF;
        }
      }
    }

    &--linkedin {
      &:hover {
        rect {
          fill: #0a66c2;
        }
      }
    }

    &--x {
      &:hover {
        rect {
          fill: #000000;
        }
      }
    }
  }
}