.footer__main {
  @include c-separator(top);

  padding: 100px 0 0;

  .wrapper {
    display: grid;
    grid-template-columns: 1fr 2.75fr 1fr;
    gap: 60px;
    max-width: 1540px;
  }
}

.footer__logos {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 275px;

  svg {
    max-width: 240px;
  }
}

.footer__pledge {
  display: flex;
  gap: 50px;
}

.footer__nav {
  display: flex;
  justify-content: space-around;
  gap: 40px;

  header,
  a {
    font-size: 20px;
    color: black;
  }

  a {
    @include do--menu-links;
  }

  strong {
    font-weight: 600;
  }

  header {
    font-weight: 600;
  }

  > div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
  }
}

.footer__cta {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 285px;
  padding: 30px 40px;
  border-radius: 10px;
  background-color: #EFFBFF;

  em {
    margin-left: -4px;
  }
}

.footer__copyright {
  margin-top: 50px;
  margin-bottom: 20px;

  &.wrapper,
  .wrapper {
    grid-template-columns: 1fr;
  }

  .wrapper {
    @include c-separator(top, 2px, multicolor);

    grid-template-columns: 1fr 1fr;
    align-items: center;
    padding: 30px 0 0 0;
  }

  nav {
    display: flex;
    align-items: center;
    gap: 30px;

    a {
      path {
        fill: #1C1C1C;
				transition: fill 0.2s ease;
      }
      &:nth-child(1) {
        &:hover {
          path {
            fill: #0a66c2
          }
        }
      }

      &:nth-child(2) {
        &:hover {
          path {
            fill: #FF0000
          }
        }
      }

      &:not(:has(svg)) {
        @include do--menu-links;
      }
    }
  }

  p {
    text-align: right;
  }
}