.section--our-story {
  @include respond-to('ismobile') {
    background-size: 90%;
  }

  position: relative;
  z-index: 10;
  background: url('./img/lines--orange-reverse.svg') left -50px / auto no-repeat;

  .wrapper {
    padding-top: 12vw;
    padding-bottom: 5vw;
  }

  img {
    display: block;
    border-radius: 10px;
    transform: translateY(-50px);
    opacity: 0;
    animation: do--fade-down 1s 0s ease forwards;
  }
}

.leadership {
  @include respond-to('tablet-v') {
    padding-top: 100px;
  }

  padding-top: 5vw;
  padding-bottom: 15vw;
  
  > header {
    padding-bottom: 50px;
    text-align: center;

    h2 {
      margin-top: 25px;
    }
  }

  .leaders {
    @include respond-to('ismobile') {
      gap: 35px;
    }
    @include respond-to('tablet-v') {
      grid-template-columns: 1fr 1fr;
      gap: 45px;
    }
    @include respond-to('leadership') {
      grid-template-columns: 1fr;
      gap: 45px;
    }

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 65px;
  }

  figure {
    padding: 0 16px 13px 0;
    border-radius: 30px;
    overflow: hidden;
  }

  img {
    display: block;
    width: 100%;
    border-radius: 30px;
    transition: box-shadow .25s ease;
  }

  h3 {
    margin: 0;
    font-size: 24px;
    font-weight: 600;
  }

  p {
    margin: 0;
  }

  footer {
    display: flex;
    gap: 10px;
  }

  a {
    padding: 5px;
    color: $suit-color;
    opacity: .55;

    &:hover {
      opacity: 1;

      &.icon-x {
        color: black;
      }

      &.icon-linkedin {
        color: #0a66c2;
      }
    }
  }

  .leader {
    width: calc(100% + 16px);
    
    header {
      padding: 25px 0 15px 10px;
    }

    footer {
      padding-left: 6px;
      font-size: 22px;
    }

    &:hover {
      figure {
        img {
          box-shadow: 24px 25px 0 -10px rgba(#D4FB84, .5);
        }
      }
    }
  }
}